const ClassDetail = () => import('./class/detail')

const routes = [{
  path: '',
  name: 'ActivityIndex',
  redirect: 'class/detail'
}, {
  path: 'class/detail',
  name: 'ActivityClassDeatil',
  component: ClassDetail
}, {
  path: 'weixin/class/detail',
  name: 'ActivityWeixinClassDeatil',
  component: ClassDetail
}]

export default routes



