<template>
  <div class="hello">
    <h1 @click="click">{{ msg }}</h1>
    <div>
      <h2>测试页面</h2>
      <router-link to="/demo/page1">DemoPage1</router-link>
      <router-link to="/demo/page2">DemoPage2</router-link>
    </div>
    <div>
      <h2>正式页面</h2>
      <router-link to="/activity/class/detail">活动详情</router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      msg: "Welcome to Your Vue.js App"
    };
  },
  methods: {
    click() {
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



