// 把组件按组分块
// 有时候我们想把某个路由下的所有组件都打包在同个异步块 (chunk) 中。只需要使用 命名 chunk，一个特殊的注释语法来提供 chunk name (需要 Webpack > 2.4)。
// const Foo = () => import(/* webpackChunkName: "group-foo" */ './Foo.vue')
// const Bar = () => import(/* webpackChunkName: "group-foo" */ './Bar.vue')
// const Baz = () => import(/* webpackChunkName: "group-foo" */ './Baz.vue')
// Webpack 会将任何一个异步模块与相同的块名称组合到相同的异步块中。
const DemoPage1Component = () => import('./demo-page1')
const DemoPage2Component = () => import('./demo-page2')
const DemoPage3Component = () => import('./demo-page3')
const DemoPage4Component = () => import('./demo-page4')

const routes = [{
  path: 'page1',
  name: 'DemoPage1',
  component: DemoPage1Component
}, {
  path: 'page2',
  name: 'DemoPage2',
  component: DemoPage2Component
}, {
  path: 'page3',
  name: 'DemoPage3',
  component: DemoPage3Component
}, {
  path: 'page4',
  name: 'DemoPage4',
  component: DemoPage4Component
}]

export default routes



