import Vue from 'vue'
import Router from 'vue-router'
import HomeComponent from '@/pages/home'
import DemoRoutes from '@/pages/demo/router'
import ActivityRoutes from '@/pages/activity/router'
import PayRoutes from '@/pages/pay/router'
const NotFoundComponent = () => import('@/pages/others/not-found')

Vue.use(Router)

// 通用承载嵌套路由的Component
const ChildRouterView = {
  template: '<router-view/>'
}

export default new Router({
  mode: 'history',
  routes: [{
    // 首页
    path: '/',
    name: 'Home',
    component: HomeComponent,
    redirect: '/activity'
  }, {
    // 路由划分，各个模块自己那边定义，保证路由清晰
    path: '/demo',
    name: 'DemoPages',
    component: ChildRouterView,
    children: DemoRoutes
  }, {
    // 活动
    path: '/activity',
    name: 'Activity',
    component: ChildRouterView,
    children: ActivityRoutes
  }, {
    // 支付
    path: '/pay',
    name: 'Pay',
    component: ChildRouterView,
    children: PayRoutes
  }, {
    // 会匹配所有路径
    path: '*',
    name: '404',
    component: NotFoundComponent
  }]
})



