<template>
  <div
    class="base-spinner-double-bounce"
    :style="{
      width: spinnerSize,
      height: spinnerSize
    }"
  >
    <div class="base-spinner-double-bounce-bounce1" :style="{ backgroundColor: spinnerColor }"></div>
    <div class="base-spinner-double-bounce-bounce2" :style="{ backgroundColor: spinnerColor }"></div>
  </div>
</template>

<script>
import common from "./base-spinner-common.vue"

export default {
  name: "double-bounce",

  mixins: [common]
}
</script>

<style lang="css">
@component-namespace base-spinner {
  @component double-bounce {
    position: relative;

    @descendent bounce1, bounce2 {
      width: 100%;
      height: 100%;
      border-radius: 50%;
      opacity: 0.6;
      position: absolute;
      top: 0;
      left: 0;

      animation: base-spinner-double-bounce 2s infinite ease-in-out;
    }

    @descendent bounce2 {
      animation-delay: -1s;
    }
  }
}

@keyframes base-spinner-double-bounce {
  0%,
  100% {
    transform: scale(0);
  }

  50% {
    transform: scale(1);
  }
}

.base-spinner-double-bounce {
  position: relative;
}
.base-spinner-double-bounce-bounce1,
.base-spinner-double-bounce-bounce2 {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  opacity: 0.6;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-animation: base-spinner-double-bounce 2s infinite ease-in-out;
  animation: base-spinner-double-bounce 2s infinite ease-in-out;
}
.base-spinner-double-bounce-bounce2 {
  -webkit-animation-delay: -1s;
  animation-delay: -1s;
}
@-webkit-keyframes base-spinner-double-bounce {
  0%,
  100% {
    -webkit-transform: scale(0);
    transform: scale(0);
  }
  50% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}
@keyframes base-spinner-double-bounce {
  0%,
  100% {
    -webkit-transform: scale(0);
    transform: scale(0);
  }
  50% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}
</style>



