<template>
  <span><component :is="spinner"></component></span>
</template>

<!-- 使用方法请参考：http://mint-ui.github.io/docs/#/zh-cn/spinner -->
<script>
import BaseSpinnerSnake from './base-spinner-snake.vue'
import BaseSpinnerDoubleBounce from './base-spinner-double-bounce.vue'
import BaseSpinnerTripleBounce from './base-spinner-triple-bounce.vue'
import BaseSpinnerFadingCircle from './base-spinner-fading-circle.vue'
const SPINNERS = [
  'snake',
  'double-bounce',
  'triple-bounce',
  'fading-circle'
]
const parseSpinner = function(index) {
  if ({}.toString.call(index) === '[object Number]') {
    if (SPINNERS.length <= index) {
      console.warn(`'${index}' spinner not found, use the default spinner.`)
      index = 0
    }
    return SPINNERS[index]
  }

  if (SPINNERS.indexOf(index) === -1) {
    console.warn(`'${index}' spinner not found, use the default spinner.`)
    index = SPINNERS[0]
  }
  return index
};

/**
 * base-spinner
 * @module components/spinner
 * @desc 加载动画
 * @param {(string|number)} [type=snake] - 显示类型，传入类型名或者类型 id，可选 `snake`, `dobule-bounce`, `triple-bounce`, `fading-circle`
 * @param {number} size - 尺寸
 * @param {string} color - 颜色
 *
 * @example
 * <base-spinner type="snake"></base-spinner>
 *
 * <!-- double-bounce -->
 * <base-spinner :type="1"></base-spinner>
 *
 * <!-- default snake -->
 * <base-spinner :size="30" color="#999"></base-spinner>
 */
export default {
  name: 'base-spinner',

  computed: {
    spinner() {
      return `spinner-${parseSpinner(this.type)}`
    }
  },

  components: {
    'spinner-snake': BaseSpinnerSnake,
    'spinner-double-bounce': BaseSpinnerDoubleBounce,
    'spinner-triple-bounce': BaseSpinnerTripleBounce,
    'spinner-fading-circle': BaseSpinnerFadingCircle
  },

  props: {
    type: {
      default: 0
    },
    size: {
      type: Number,
      default: 28
    },
    color: {
      type: String,
      default: '#ccc'
    }
  }
}
</script>



