// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import VueLazyload from 'vue-lazyload'
import App from './app'
import router from './appconfig/routerconfig'
import MessageBox from '@/components/message-box'
import Toast from '@/components/toast'
import Indicator from '@/components/indicator'
import { currency, formatDate } from '@/core/pipes'
import VueClipboard from 'vue-clipboard2'
import { Router } from '@/core/router/Router'
import staticRouter from '../build/router'
import VueCookies from 'vue-cookies'

Vue.config.productionTip = false
VueCookies.config('7d')

Vue.use(VueLazyload)
Vue.use(VueClipboard)
Vue.use(VueCookies)
// 全局注入通用组件，无需重复引用
Vue.$messagebox = Vue.prototype.$messagebox = MessageBox
Vue.$toast = Vue.prototype.$toast = Toast
Vue.$indicator = Vue.prototype.$indicator = Indicator
Vue.$navigator = Vue.prototype.$navigator = Router
Vue.$cookies = Vue.prototype.$cookies = VueCookies

// 全局注入通用管道
Vue.filter('currency', currency)
Vue.filter('formatDate', formatDate)

// eslint-disable-next-line no-new
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>',
  mounted () {
    document.dispatchEvent(new Event('render-event'))
  },
  created () {
    // 单页时自动匹配并设置页面标题
    let path = staticRouter.maps.find(f => f.path === location.pathname)
    if (path) {
      document.title = path.title
    }
  }
})



