import Uri from 'jsuri'

export class Router {
  static navigate (url, query = null) {
    let uri = new Uri(url)
    if (query) {
      for (let key in query) {
        if (uri.getQueryParamValue(key)) {
          uri.deleteQueryParam(key)
        }
        uri.addQueryParam(key, query[key])
      }
    }
    url = uri.toString()
    this.webNavigate(url)
  }

  static webNavigate (url) {
    let uri = new Uri(url)
    this.routeWeb(uri, url)
  }

  static routeWeb (uri, url) {
    if (uri.getQueryParamValue('redirect') || uri.getQueryParamValue('back')) {
      location.replace(url)
    } else {
      location.href = url
    }
  }

  static h5BaseUrl () {
    let uri = new Uri(location.href)
    return new Uri().setProtocol(uri.protocol()).setHost(uri.host()).setPort(uri.port()).toString()
  }
}



