<template>
  <transition name="base-toast-pop">
    <div
      class="base-toast"
      v-show="visible"
      :class="customClass"
      :style="{ 'padding': iconClass === '' ? '10px' : '20px' }"
    >
      <i class="base-toast-icon" :class="iconClass" v-if="iconClass !== ''"></i>
      <span
        class="base-toast-text"
        :style="{ 'padding-top': iconClass === '' ? '0' : '10px' }"
      >{{ message }}</span>
    </div>
  </transition>
</template>

<!-- 使用方法请参考：http://mint-ui.github.io/docs/#/zh-cn/toast -->
<script>
export default {
  props: {
    message: String,
    className: {
      type: String,
      default: ""
    },
    position: {
      type: String,
      default: "middle"
    },
    iconClass: {
      type: String,
      default: ""
    }
  },

  data() {
    return {
      visible: false
    }
  },

  computed: {
    customClass() {
      var classes = [];
      switch (this.position) {
        case "top":
          classes.push("is-placetop")
          break
        case "bottom":
          classes.push("is-placebottom")
          break
        default:
          classes.push("is-placemiddle")
      }
      classes.push(this.className)

      return classes.join(" ")
    }
  }
}
</script>

<style>
@component-namespace base {
  @component toast {
    position: fixed;
    max-width: 80%;
    border-radius: 5px;
    background: rgba(0, 0, 0, 0.7);
    color: #fff;
    box-sizing: border-box;
    text-align: center;
    z-index: 1000;
    transition: opacity 0.3s linear;

    @descendent icon {
      display: block;
      text-align: center;
      font-size: 56px;
    }

    @descendent text {
      font-size: 14px;
      display: block;
      text-align: center;
    }

    @when placetop {
      top: 50px;
      left: 50%;
      transform: translate(-50%, 0);
    }

    @when placemiddle {
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
    }

    @when placebottom {
      bottom: 50px;
      left: 50%;
      transform: translate(-50%, 0);
    }

    @descendent pop-enter, pop-leave-active {
      opacity: 0;
    }
  }
}

.base-toast {
  position: fixed;
  max-width: 80%;
  border-radius: 5px;
  background: rgba(0, 0, 0, 0.7);
  color: #fff;
  box-sizing: border-box;
  text-align: center;
  z-index: 1000;
  -webkit-transition: opacity 0.3s linear;
  transition: opacity 0.3s linear;
}
.base-toast.is-placebottom {
  bottom: 50px;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
  transform: translate(-50%, 0);
}
.base-toast.is-placemiddle {
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
.base-toast.is-placetop {
  top: 50px;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
  transform: translate(-50%, 0);
}
.base-toast-icon {
  display: block;
  text-align: center;
  font-size: 56px;
}
.base-toast-text {
  font-size: 14px;
  display: block;
  text-align: center;
}
.base-toast-pop-enter,
.base-toast-pop-leave-active {
  opacity: 0;
}
</style>



