<template>
  <div class="base-spinner-triple-bounce">
    <div class="base-spinner-triple-bounce-bounce1" :style="bounceStyle"></div>
    <div class="base-spinner-triple-bounce-bounce2" :style="bounceStyle"></div>
    <div class="base-spinner-triple-bounce-bounce3" :style="bounceStyle"></div>
  </div>
</template>

<script>
import common from "./base-spinner-common.vue"

export default {
  name: "triple-bounce",

  mixins: [common],

  computed: {
    spinnerSize() {
      return (this.size || this.$parent.size || 28) / 3 + "px"
    },

    bounceStyle() {
      return {
        width: this.spinnerSize,
        height: this.spinnerSize,
        backgroundColor: this.spinnerColor
      }
    }
  }
};
</script>

<style lang="css">
@component-namespace base-spinner {
  @component triple-bounce {
    @descendent bounce1, bounce2, bounce3 {
      border-radius: 100%;
      display: inline-block;
      animation: base-spinner-triple-bounce 1.4s infinite ease-in-out both;
    }

    @descendent bounce1 {
      animation-delay: -0.32s;
    }

    @descendent bounce2 {
      animation-delay: -0.16s;
    }
  }
}

@keyframes base-spinner-triple-bounce {
  0%,
  80%,
  100% {
    transform: scale(0);
  }
  40% {
    transform: scale(1);
  }
}

.base-spinner-triple-bounce {
}
.base-spinner-triple-bounce-bounce1,
.base-spinner-triple-bounce-bounce2,
.base-spinner-triple-bounce-bounce3 {
  border-radius: 100%;
  display: inline-block;
  -webkit-animation: base-spinner-triple-bounce 1.4s infinite ease-in-out both;
  animation: base-spinner-triple-bounce 1.4s infinite ease-in-out both;
}
.base-spinner-triple-bounce-bounce1 {
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}
.base-spinner-triple-bounce-bounce2 {
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}
@-webkit-keyframes base-spinner-triple-bounce {
  0%,
  80%,
  100% {
    -webkit-transform: scale(0);
    transform: scale(0);
  }
  40% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}
@keyframes base-spinner-triple-bounce {
  0%,
  80%,
  100% {
    -webkit-transform: scale(0);
    transform: scale(0);
  }
  40% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}
</style>



