<template>
  <transition name="base-indicator">
    <div class="base-indicator" v-show="visible">
      <div class="base-indicator-wrapper" :style="{ 'padding': text ? '20px' : '15px' }">
        <spinner class="base-indicator-spin" :type="convertedSpinnerType" :size="32"></spinner>
        <span class="base-indicator-text" v-show="text">{{ text }}</span>
      </div>
      <div class="base-indicator-mask" @touchmove.stop.prevent></div>
    </div>
  </transition>
</template>
<!-- 使用方法请参考：http://mint-ui.github.io/docs/#/zh-cn/indicator -->
<script type="text/babel">
  import Spinner from '../spinner/base-spinner'

  export default {
    data() {
      return {
        visible: false
      }
    },

    components: {
      Spinner
    },

    computed: {
      convertedSpinnerType() {
        switch (this.spinnerType) {
          case 'double-bounce':
            return 1
          case 'triple-bounce':
            return 2
          case 'fading-circle':
            return 3
          default:
            return 0
        }
      }
    },

    props: {
      text: String,
      spinnerType: {
        type: String,
        default: 'snake'
      }
    }
  }
</script>

<style>
@component-namespace base {
  @component indicator {
    transition: opacity .2s linear;

    @descendent wrapper {
      position: fixed 50% * * 50%;
      transform: translate(-50%, -50%);
      border-radius: 5px;
      background: rgba(0, 0, 0, 0.7);
      color: white;
      box-sizing: border-box;
      text-align: center;
      z-index: 3000;
    }

    @descendent text {
      display: block;
      color: #fff;
      text-align: center;
      margin-top: 10px;
      font-size: 16px;
    }

    @descendent spin {
      display: inline-block;
      text-align: center;
    }

    @descendent mask {
      position: fixed 0 * * 0;
      size: 100%;
      opacity: 0;
      background: transparent;
      z-index: 3000;
    }
  }
}

.base-indicator-enter,
.base-indicator-leave-active {
  opacity: 0;
}


.base-indicator {
  -webkit-transition: opacity .2s linear;
  transition: opacity .2s linear;
}
.base-indicator-wrapper {
  top: 50%;
  left: 50%;
  position: fixed;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  border-radius: 5px;
  background: rgba(0, 0, 0, 0.7);
  color: white;
  box-sizing: border-box;
  text-align: center;
}
.base-indicator-text {
  display: block;
  color: #fff;
  text-align: center;
  margin-top: 10px;
  font-size: 16px;
}
.base-indicator-spin {
  display: inline-block;
  text-align: center;
}
.base-indicator-mask {
  top: 0;
  left: 0;
  position: fixed;
  width: 100%;
  height: 100%;
  opacity: 0;
  background: transparent;
}
.base-indicator-enter, .base-indicator-leave-active {
  opacity: 0;
}

</style>



